
.Node {
  outline: solid;
  outline-color: gray;
  outline-width: 2px;
  background-color: white;
  /* Nodes by default go over Links */
  z-index: 1;
}
.Node:hover {
  cursor: pointer;
}

.Tree {
  background-color: transparent;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  height: 400px;
}

@-webkit-keyframes activate {
  from {
    outline-color: gray;
    stroke: gray;
  }
  to {
    outline-color: gold;
    stroke: gold;
  }
}

@keyframes activate {
  from {
    outline-color: gray;
    stroke: gray;
  }
  to {
    outline-color: gold;
    stroke: gold;
  }
}
.Tree .active {
  -webkit-animation: 1.5s forwards activate;
          animation: 1.5s forwards activate;
}

.Tree-Row {
  display: flex;
  justify-content: space-evenly;
}

.Link-Container {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.Link {
  fill: none;
  stroke: gray;
  stroke-width: 6px;
}
